<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="msgList">
        <ToolBar>
            <div>
                <el-button type="primary" icon="el-icon-plus" size="small">新增站内消息</el-button>
                <el-button type="primary" icon="el-icon-plus" size="small">新增短信消息</el-button>
            </div>
            <div>
                <el-input
                        placeholder="搜索标题"
                        size="small"
                        style="width: 140px"
                        v-model="searchParams.postTitle"
                        clearable>
                </el-input>
                <el-select v-model="searchParams.postType" size="small" clearable placeholder="消息分类"
                           style="width: 140px">
                    <el-option
                            v-for="(v,k) in $Cfg.postType"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>

                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="消息类型 "
                           style="width: 120px">
                    <el-option
                            v-for="(v,k) in $Cfg.postStatus"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>
                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="接收人群"
                           style="width: 120px">
                    <el-option
                        v-for="(v,k) in $Cfg.postStatus"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="状态"
                           style="width: 120px">
                    <el-option
                        v-for="(v,k) in $Cfg.postStatus"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="短信平台"
                           style="width: 120px">
                    <el-option
                        v-for="(v,k) in $Cfg.postStatus"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="操作人"
                           style="width: 120px">
                    <el-option
                        v-for="(v,k) in $Cfg.postStatus"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <el-button type="success"  size="small" @click="">查询</el-button>
                <el-button type="success"  size="small" @click="">重置</el-button>
            </div>
        </ToolBar>
        <el-table
                :data="tableData"
                border
                style="width: 100%">
            <el-table-column
                    prop="date"
                    label="ID"
                    width="98">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="消息标题">
            </el-table-column>
            <el-table-column
                    prop="address"
                    label="消息分类">
            </el-table-column>
            <el-table-column
                prop="address"
                label="消息类型">
            </el-table-column>
            <el-table-column
                prop="address"
                label="接受人群">
            </el-table-column>
            <el-table-column
                prop="address"
                label="发布时间">
            </el-table-column>
            <el-table-column
                prop="address"
                label="发布状态">
            </el-table-column>
            <el-table-column
                prop="address"
                label="短信平台">
            </el-table-column>
            <el-table-column
                prop="address"
                label="操作人">
            </el-table-column>
            <el-table-column
                prop="address"
                label="创建时间">
            </el-table-column>
            <el-table-column
                    label="操作"
                    width="210">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="danger" size="small"
                            >撤回</el-button>
                    <el-button @click="seeMsg(scope.row)" type="success" size="small"
                            >预览</el-button>
                    <el-button @click="$router.push({path:'/msg/msg_list/msg_edit'})" type="primary" size="small"
                            >修改</el-button>
                </template>
            </el-table-column>
        </el-table>
        <Pagination
            :params="searchParams"
            :requestFunc="requestFunc"
            @returnData="returnData"
        />
    </div>
</template>

<script>
    import ToolBar from '@/components/ToolBar.vue';
    import HelpHint from '@/components/HelpHint.vue';
    import Pagination from '@/components/Pagination.vue';
    import paginationMixin from './mixin/page.js'
    import MsgDetails from './MsgDetails.vue';
    export default {
        mixins:[paginationMixin],
        data() {
            return {
                tableData: [{
                    date: '2016-05-03',
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }, {
                    date: '2016-05-02',
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }]
            }
        },
        methods: {
            seeMsg(row){
                let h = this.$createElement
                this.$alert(h('MsgDetails',{props:{row:row}}), '消息预览', {
                    customClass:'see-msg',
                    confirmButtonText: '确定',
                    callback: action => {}
                });
            },
            handleClick(row) {
                this.$alert(row, '标题名称', {
                    confirmButtonText: '确定',
                    callback: action => {}
                });
            },
        },
        created() {

        },
        components: {
            ToolBar, HelpHint, Pagination ,MsgDetails
        }
    }
</script>
